<template>
  <div class="home-container">
    <div class="home-wrapper">
      <div class="ads" style="padding-bottom: 0px;margin-bottom: 40px">
        <div class="ads-item" v-for="(item, idx) in _ads.slice(0, 8)" :key="item.title + '_' + idx">
          <van-image
            @click="goAd(item.link)"
            fit="cover"
            position="center"
            :lazy-load="true"
            :src="item.picUrl"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>

      <div class="category-part" v-for="(item, idx) in data" :key="item.id">
        <Title class="page-title" :name="item.category.classifyName" :suffix="suffixMap[language]" :item="item.category"></Title>
        <div class="list">
          <Card :classifyName="item.category.classifyName" :classifyId="item.category.classifyId" :specialId="item.category.specialId" class="video-card" v-for="v in item.videos" :item="v"></Card>
        </div>
        <div   class="ads h5-hide">
          <div class="ads-item" v-for="(a, idx) in item.ads.slice(0, 4)" :key="a.title + '_' + idx">
            <van-image
              @click="goAd(a.link)"
              fit="cover"
              position="center"
              :lazy-load="true"
              :src="a.picUrl"
            >
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import * as api from '@/api'
  import Title from "@/components/Title.vue";
  import Card from "@/components/Card.vue";
  import {mapActions,mapState} from 'vuex';

  export default {
    name: "Index",
    components: {Title, Card},
    data() {
      return {
        _ads: [],
        data: [],
        suffixMap: {
          'ko-KR': '더보기',
          'en-US': 'More related videos',
          'zh-CN': '更多'
        }
      }
    },
    watch: {
      menus() {
        this.getVideos()
      }
    },
    computed: {
      ...mapState({
        ads: state => state.common.ads,
        menus: state => state.common.menus,
        language: state => state.common.language,
      }),
    },
    methods: {
      ...mapActions({
        toggleLoading: 'common/toggleLoading',
      }),
      goAd(url) {
        window.open(url)
      },
      async getVideos() {
        this.toggleLoading(true)
        if (this.menus && this.menus.length) {
          let count = (this.menus.length + 1) * 8 + 1;
          let times = Math.ceil(count / this.ads.length);
          let ads = [];
          for (let i = 0;i < times;i++) {
            ads = ads.concat(this.ads);
          }
          ads.shift()
          this._ads = ads;
          let index = 0
          const func = async (menu, i) => {
            let {rows, code} = await api.getVideos({
              pageNum: 1,
              pageSize: 4,
              // classifyId: menu.classifyId,
              specialId: menu.specialId
            })
            if (code == 200) {
              this.data.push({
                id: menu.id,
                category: menu,
                ads: ads.slice((index + 1) * 8 + 4, (index + 2) * 8 + 4),
                adsSide: ads.slice((index + 1) * 5, (index + 2) * 5),
                videos: rows,
              })
              if (i % 2 == 1) {
                index = index + 1
              }

            }
          }
          for (let i = 0; i < this.menus.length; i++) {
            await func(this.menus[i], i);
            this.toggleLoading(false)
          }
        }
      }
    },
    mounted() {
      this.getVideos()
    },
  }
</script>

<style scoped lang="scss">
  .home-container{
    width: 100%;
    display: flex;
    justify-content: space-between;
    .home-wrapper{
      width: 100%;
      padding-top: 25px;
      .ads{
        width: 100%;
        padding: 23px 20px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        box-sizing: border-box;
        padding-top: 0px;
        padding-bottom: 40px;
        .ads-item{
          width: calc((100% - 27px) / 4);
          margin-bottom: 7px;
          height: 125px;
          @include border2;
          box-sizing: border-box;
          cursor: pointer;
          border-radius: 2px;
          :deep(.van-image){
            width: 100%;
            height: 100%;
            .van-image__loading{
              background: none;
            }
            .van-image__error{
              background: none;
            }
          }
        }
      }
      .page-title{
        margin-bottom: 11px;
      }
      .list{
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        flex-direction:row;
        box-sizing: border-box;
        padding-left: 20px;
        padding-right: 20px;
        margin-bottom: 20px;
        .video-card{
          margin-bottom: 20px;
        }
      }

    }
  }
</style>
<style lang="scss" scoped>

  @media screen and (min-width: 750px) and (max-width: 1280px) {
    .home-container{
      .home-wrapper{
        .ads{
          .ads-item{
            height: 100px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 750px) {
    .home-container{
      width: 100%;
      .home-wrapper{
        width: 100%;
        box-sizing: border-box;
        padding-left: 2px;
        padding-right: 2px;
        .page-title{
          margin-bottom: 10px;
        }
        .ads{
          padding-left: 14px;
          padding-right: 14px;
          .ads-item{
            width: 100%;
            height: 130px;
            margin-bottom: 6px;
            border: 1px solid #d7d7d8;
            &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4), {
              display: block;
            }
          }
        }
        .list{
          margin-bottom: 25px;
          .video-card{
            width: 100%;
            margin-bottom: 18px;
          }
        }
      }
      .sidebar{
        display: none;
      }
    }
  }
</style>
